/**
 *
 * PaneSettingsModal
 *
 */

import React from 'react';
// import styled from 'styled-components';

// import {FormattedMessage} from 'react-intl';
// import messages from './messages';

import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
  Col,
  Button,
} from 'reactstrap';

import CbToggle from '../../components/CbToggle';
import { PaneThemeTypes } from 'containers/Portal/types';

export interface ClosePaneSettingsModalRtn extends IState {
  id: string;
}

interface IProps {
  paneInfo: {
    id: string;
    title: string;
    hideHeader: boolean;
    hideBorders: boolean;
    tabbed: boolean;
    customPaneId: string;
    themeType: PaneThemeTypes;
  };
  onDismiss: () => void;
  title: string;
  onClose: (rtn: ClosePaneSettingsModalRtn) => void;
}

interface IState {
  title: string;
  hideHeader: boolean;
  hideBorders: boolean;
  tabbed: boolean;
  customPaneId: string;
  themeType: PaneThemeTypes;
}

class PaneSettingsModal extends React.PureComponent<IProps, {}> {
  state: IState = {
    title: this.props.paneInfo.title,
    hideHeader: this.props.paneInfo.hideHeader,
    hideBorders: this.props.paneInfo.hideBorders,
    tabbed: this.props.paneInfo.tabbed,
    customPaneId: this.props.paneInfo.customPaneId,
    themeType: this.props.paneInfo.themeType || 'primary',
  };

  render() {
    return (
      <Modal isOpen={true} toggle={this.props.onDismiss} autoFocus={false}>
        <ModalHeader>{this.props.title}</ModalHeader>

        <ModalBody>
          <Form onSubmit={this._onSubmit}>
            <FormGroup row>
              <Col sm={3}>
                <Label>Title</Label>
              </Col>
              <Col sm={9}>
                <Input
                  type="text"
                  placeholder="My Pane Title"
                  value={this.state.title}
                  autoFocus={this.state.title === ''}
                  onChange={this.onSettingChange.bind(this, 'title')}
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col sm={3}>
                <Label>Hide Header</Label>
              </Col>
              <Col sm={9}>
                <CbToggle
                  value={this.state.hideHeader}
                  onChange={this._onHideHeaderSelected}
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col sm={3}>
                <Label>Hide Borders</Label>
              </Col>
              <Col sm={9}>
                <CbToggle
                  value={this.state.hideBorders}
                  onChange={this._onHideBordersSelected}
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col sm={3}>
                <Label>Tabbed Pane</Label>
              </Col>
              <Col sm={9}>
                <CbToggle
                  onChange={this._onTabbedPaneSelected}
                  value={this.state.tabbed}
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col sm={3}>
                <Label>Theme</Label>
              </Col>
              <Col sm={9}>
                <Input
                  type="select"
                  value={this.state.themeType}
                  onChange={this._onThemeChange}
                >
                  <option value="primary">Primary</option>
                  <option value="secondary">Secondary</option>
                </Input>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col sm={3}>
                <Label>Pane ID</Label>
              </Col>
              <Col sm={9}>
                <Input
                  type="text"
                  placeholder="This Pane's ID"
                  value={this.state.customPaneId}
                  onChange={this.onSettingChange.bind(this, 'customPaneId')}
                />
              </Col>
            </FormGroup>

            {/*todo: add this back once we figure out how to hide panes at certain screen sizes
             <FormGroup controlId="formHorizontalEmail">
             <Col sm={3} componentClass={ControlLabel}>Screen Sizes</Col>
             <Col sm={9}>

             {this._renderScreenSizeCheckboxes(this.state.breakpoints)}

             </Col>
             </FormGroup>*/}
          </Form>
        </ModalBody>

        <ModalFooter>
          <Button color="default" onClick={this.props.onDismiss}>
            Cancel
          </Button>
          <Button color="primary" onClick={this._onSubmit}>
            Apply
          </Button>
        </ModalFooter>
      </Modal>
    );
  }

  // _onChangeVisibility(key) {
  //   //todo: figure out if this is mutating state before calling setState
  //   let breakpoints = this.state.breakpoints;
  //   breakpoints[key].isVisible = !breakpoints[key].isVisible;
  //   this.setState({ breakpoints });
  // }

  onSettingChange(key: keyof IState, e: React.ChangeEvent<HTMLInputElement>) {
    this.setState({
      ...this.state,
      [key]: e.target.value,
    });
  }

  _onTabbedPaneSelected = () => {
    this.setState({ tabbed: !this.state.tabbed });
  };

  _onHideHeaderSelected = () => {
    this.setState({ hideHeader: !this.state.hideHeader });
  };

  _onHideBordersSelected = () => {
    this.setState({ hideBorders: !this.state.hideBorders });
  };

  _onThemeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    this.setState({
      ...this.state,
      themeType: e.target.value,
    });
  };

  _onSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const rtn = {
      ...this.state,
      id: this.props.paneInfo.id,
    };
    this.props.onClose(rtn);
  };

  // _renderScreenSizeCheckboxes = (breakpoints) => {
  //   let rtn = [];
  //   for (const key in breakpoints) {
  //     if (breakpoints.hasOwnProperty(key)) {
  //       rtn.push(
  //         <Checkbox
  //           checked={breakpoints[key].isVisible}
  //           key={key}
  //           onChange={this._onChangeVisibility.bind(this, key)}
  //         >
  //           {key}:{breakpoints[key].threshold}
  //         </Checkbox>,
  //       );
  //     }
  //   }
  //   return rtn;
  // };
}

export default PaneSettingsModal;



